/*
navigation：input输入组件
*/
var FamousEngine = require('famous/core/FamousEngine');
var DOMElement   = require('famous/dom-renderables/DOMElement');
var Node = require('famous/core/Node');

function inputCustom(){
	Node.call(this);
	this
		.setSizeMode(0,1)
		.addUIEvent('input')
		.el = new DOMElement(this,{
			classes:['input-custom'],
			// properties:{'border':'none','background':'none'},
			tagName:'input'
		});
	this.inputEvent="";
}
inputCustom.prototype = Object.create(Node.prototype);
inputCustom.prototype.constructor = inputCustom;
inputCustom.prototype.onReceive = function(e,p){
	if(e=="input"){
		if(this.inputEvent){this.inputEvent(e,p);}
	}
};

module.exports = inputCustom;